@import url('https://fonts.googleapis.com/css?family=Inconsolata');

/* Pallete
- #2e374d main blue
*/

.App {
  font-family: 'Inconsolata', monospace;
  text-align: center;
}

.boxContainer{
  display: inline-flex;
  flex-wrap: wrap;
  animation: fadeIn 3s linear;
}


.box{
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50% 25% 25%;
  grid-row-gap: 15px;
  padding-right: 50px;
  padding-left: 50px;
  margin: auto;
}


.logobox{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  align-self: center;
}

.headerbox{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 45px;
  align-self: center;
}
p{
  padding: 15px;
}

.textbox{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4;
  font-size: 20px;
  align-self: center;
}

.contactBox{
  background-image: linear-gradient(to right, rgb(66, 117, 151) , rgb(0, 60, 128));
  width: 100%;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.contactText{
  color: white;
  font-size: 15px;
  align-self: center;
  padding-top: 25px;
}

@media screen and (min-width: 800px) {

  .boxContainer{
    display: inline-flex;
    flex-wrap: wrap;
    animation: fadeIn 3s linear;
  }

  
  .box{
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 50% 25% 25%;
    grid-row-gap: 8px;
    padding-right: 50px;
    padding-left: 50px;
  }
  
  
  .logobox{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  
  .headerbox{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    font-size: 45px;
  }
  
  .textbox{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
    font-size: 20px;
  }
  
  p{
    padding: 100px;
  }
  
  span + span {
    margin-left: 10px;
  }
  
  .contactHeader{
    padding-top: 15px;
    color: white;
    font-size: 80px;
    align-self: center;
  }
  
  .contactText{
    color: white;
    font-size: 20px;
    align-self: center;
    padding-top: 50px;
  }
}

@keyframes fadeIn {
  from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

