@import url(https://fonts.googleapis.com/css?family=Inconsolata);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Pallete
- #2e374d main blue
*/

.App {
  font-family: 'Inconsolata', monospace;
  text-align: center;
}

.boxContainer{
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-animation: fadeIn 3s linear;
          animation: fadeIn 3s linear;
}


.box{
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50% 25% 25%;
  grid-row-gap: 15px;
  padding-right: 50px;
  padding-left: 50px;
  margin: auto;
}


.logobox{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  align-self: center;
}

.headerbox{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 45px;
  align-self: center;
}
p{
  padding: 15px;
}

.textbox{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4;
  font-size: 20px;
  align-self: center;
}

.contactBox{
  background-image: linear-gradient(to right, rgb(66, 117, 151) , rgb(0, 60, 128));
  width: 100%;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.contactText{
  color: white;
  font-size: 15px;
  -webkit-align-self: center;
          align-self: center;
  padding-top: 25px;
}

@media screen and (min-width: 800px) {

  .boxContainer{
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-animation: fadeIn 3s linear;
            animation: fadeIn 3s linear;
  }

  
  .box{
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 50% 25% 25%;
    grid-row-gap: 8px;
    padding-right: 50px;
    padding-left: 50px;
  }
  
  
  .logobox{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  
  .headerbox{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    font-size: 45px;
  }
  
  .textbox{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
    font-size: 20px;
  }
  
  p{
    padding: 100px;
  }
  
  span + span {
    margin-left: 10px;
  }
  
  .contactHeader{
    padding-top: 15px;
    color: white;
    font-size: 80px;
    -webkit-align-self: center;
            align-self: center;
  }
  
  .contactText{
    color: white;
    font-size: 20px;
    -webkit-align-self: center;
            align-self: center;
    padding-top: 50px;
  }
}

@-webkit-keyframes fadeIn {
  from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@keyframes fadeIn {
  from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}


ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  position: fixed;
  width: 100%;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

li {
  float: right;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  padding: 0;
}

li > * {
  display: block;
  color: white;
  text-align: center;
  padding: 1.5em 1em;
  text-decoration: none;
}

img {
  height: 2em;
  padding: 0;
}

/* Change the link color to #111 (black) on hover */
li:hover {
  background-color: #111;
} 

.backgroundHeader{
  width: 100%;
  height: 400pt;
  /* background-image: linear-gradient(to right, rgb(66, 117, 151) , rgb(0, 60, 128)); */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.upperImageContainer{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;
  width: 100%;
  height: 100%;  
}

img {
  width: 10em;
}

.BigText{
  color: white;
  font-size: 80px;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  align-self: center;
  -webkit-animation: fadeIn 2s linear;
          animation: fadeIn 2s linear;
}
.contactBox {
    margin-top: 2em;
}

.child-items * {
    color: #ffffff;
    font-size: 1.5em;
    margin: 0;
    padding: 1em 2em;
}

.child-items .odd {
    background-color: rgba(255, 255, 255, 0.411);
}

.child-items table * {
    font-size: 1em;
    margin: 0;
    padding: 0.25em 1em;
}

.child-items {
    -webkit-align-items: center;
            align-items: center;
}

.child-items h2, .child-items h3 {
    color: #2E374D;
    background-color: rgba(164, 178, 207, 0.541);
}
