ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  position: fixed;
  width: 100%;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

li {
  float: right;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

li > * {
  display: block;
  color: white;
  text-align: center;
  padding: 1.5em 1em;
  text-decoration: none;
}

img {
  height: 2em;
  padding: 0;
}

/* Change the link color to #111 (black) on hover */
li:hover {
  background-color: #111;
} 