
.backgroundHeader{
  width: 100%;
  height: 400pt;
  /* background-image: linear-gradient(to right, rgb(66, 117, 151) , rgb(0, 60, 128)); */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.upperImageContainer{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;
  width: 100%;
  height: 100%;  
}

img {
  width: 10em;
}

.BigText{
  color: white;
  font-size: 80px;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  align-self: center;
  animation: fadeIn 2s linear;
}