.contactBox {
    margin-top: 2em;
}

.child-items * {
    color: #ffffff;
    font-size: 1.5em;
    margin: 0;
    padding: 1em 2em;
}

.child-items .odd {
    background-color: rgba(255, 255, 255, 0.411);
}

.child-items table * {
    font-size: 1em;
    margin: 0;
    padding: 0.25em 1em;
}

.child-items {
    align-items: center;
}

.child-items h2, .child-items h3 {
    color: #2E374D;
    background-color: rgba(164, 178, 207, 0.541);
}